.footer{
    width: 100%;
    padding: 6rem 0;
    background-color:black;
}

.footer_container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2 , 1fr);
    grid-gap: 40px;
}

.left{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.location{
    display: flex;
    margin-bottom: 0.8rem;
}

.location p{
    line-height: 30px;
}

.right{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}
.social{
    margin-top: 1rem;

}

h4{
    padding-bottom: 0.7rem;
}
p{
    font-size: 1.2rem;
}
h5{
    padding: 0.3rem;
}

@media screen and (max-width: 640px) {
    .footer_container{
        grid-template-columns: 1fr;
    }
}