.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;

}

.header-bg{
    background-color: rgba(0, 0, 0, 0.85);
    transition: 0.5s ;
}



.nav_menu{
    display: flex;
}
.nav_menu li{
    padding: 0 10px;
    text-decoration: none;
}

.nav_menu li a{
    font-size: 1rem;
    font-weight: 500;
}

.hambarger{
    display: none;
}

@media screen and (max-width: 1040px){
    .nav_menu{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background: rgba(0, 0, 0, 0.9);
        position: absolute;
        top:0;
        left: -100%;
        z-index: -3;
        transition: 0.3s;
    }
    .nav_menu.active{
        left: 0;
    }
    .nav_menu li{
        padding: 1rem 0;
    }
    .nav_menu li a{
       font-size: 1.3rem;
    }


    .hambarger{
        display: initial;
    }
    
  }